<!--  -->
<template>
    <div class="m-feedback m-sideblock">
        <div class="m-feedback-header m-sideblock-header">
            <i class="el-icon-s-opportunity">
            </i><a class="u-title"></a>
            <a href="/bbs/#/feedback" class="u-more" title="" target="_blank"
                ><i class="el-icon-more"></i
            ></a>
        </div>
        <div class="m-feedback-publish">
            <!-- <el-alert class="u-msg" title="admin@jx3box.com" type="warning" show-icon></el-alert> -->
            <el-input
                v-model="post.post_content"
                type="textarea"
                placeholder="..."
                show-word-limit
                :maxlength="1024"
                :rows="3"
            ></el-input>
            <div class="m-feedback-actions">
                <el-upload
                    ref="upload"
                    class="u-upload avatar-uploader"
                    :action="url"
                    list-type="picture-card"
                    :on-remove="remove"
                    :on-success="done"
                    :on-error="fail"
                    :on-exceed="exceed"
                    :limit="max"
                    title=""
                    with-credentials
                    accept="image/jpg, image/jpeg, image/gif, image/png, image/bmp"
                >
                    <i class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
                <div class="u-submit">
                    <el-button
                        :loading="processing"
                        @click="publish"
                        icon="el-icon-s-promotion"
                        size="small"
                        plain
                    ></el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { __cms } from "@jx3box/jx3box-common/data/jx3box.json";
const API_Root = process.env.NODE_ENV === "production" ? __cms : "/";
const API = API_Root + "api/cms/upload";
import User from "@jx3box/jx3box-common/js/user";
import { push } from "@/service/cms.js";
export default {
    name: 'feedback',
    data() {
        return {
            imgs: [],
            url: API,

            max: 8,
            // 
            post: {
                // ID
                ID: "",
                // publishprivatedraftdustbin
                post_status: "publish",
                // 
                post_type: "feedback",

                // 
                post_title: "",
                // 
                post_subtype: "0",
                // 
                post_meta: {},
                // 
                post_content: "",
                // ()
                post_mode: "",

                // 
                original: 1,
                // stdorigin
                client: this.client || "all",
                // cntr
                lang: "cn",
                // 
                zlp: "",

                // 
                post_excerpt: "",
                // 
                post_banner: "",
                // 
                post_collection: "",

                // 0|1
                comment: 0,

                // 012345
                visible: 0,
                //  10
                tags: [],
            },
            processing: false,
            user: User.getInfo(),
            isLogin: User.isLogin()
        }
    },
    computed: {
        client: function (){
            return this.$store.state.client
        }
    },
    methods: {
        // 
        done: function (res) {
            this.imgs = [...this.imgs, res.data[0]];
        },
        // 
        fail: function (err) {
            try {
                let response = JSON.parse(err.message);
                this.$message.error(`[${response.code}]${response.msg}`);
            } catch {
                this.$message.error("");
            }
        },
        // 
        exceed: function () {
            this.$message.warning(`${this.max}`);
        },
        // 
        remove: function (file) {
            this.imgs = this.imgs.filter(
                (img) => img !== file?.response?.data[0]
            );
        },
        // 
        publish: function () {
            if (!this.isLogin) {
                if (this.post.post_content) {
                    localStorage.setItem('feedback', this.post.post_content);
                }
                User.toLogin();
                return
            }
            this.post.post_meta = this.imgs;
            this.processing = true;
            this.post.post_excerpt = this.post.post_content.slice(0, 200);

            push(this.post)
                .then((res) => {
                    let result = res.data.data;
                    this.$emit("update", {
                        ...result,
                        author_info: {
                            user_avatar: this.user?.avatar_origin,
                            display_name: this.user?.name
                        }
                    });
                    this.$notify.success({
                        title: "",
                        dangerouslyUseHTMLString: true,
                        message: `<div></div>
                                <div style="margin-top: 5px;"><a href="/bbs/#feedback/${result.ID}"></a></div>`,
                        duration: 6000
                    });

                    // 
                    this.post = this.$options.data().post;
                    this.imgs = [];
                    this.$refs.upload.clearFiles()
                })
                // .catch((err) => {
                //     this.$notify.error({ title: "", message: "" });
                // })
                .finally(() => {
                    this.processing = false;
                });
        },
        loadCache: function (){
            const content = localStorage.getItem('feedback') || '';
            this.post.post_content = content;
            localStorage.removeItem('feedback');
        }
    },
    mounted: function (){
        this.loadCache()
    }
}
</script>

<style lang="less">
@import "../assets/css/feedback.less";
</style>